<template>
  <div class="pageContent">
    <el-card shadow="always">
      <el-form label-width="80px">
        <div v-for="(item,index) in list" :key="index">
          <el-divider v-if="index != 0"></el-divider>
          <el-form-item label="版本号">
            <div>{{item.codeVersion}}</div>
          </el-form-item>
          <el-form-item label="更新时间">
            <div>{{item.createTimeStr}}</div>
          </el-form-item>
          <el-form-item label="更新内容">
            <div>{{item.codeDesc}}</div>
          </el-form-item>
        </div>
        
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getCodeHistory } from '@/api/weChat/miniManage'
export default {
  data(){
    return{
      list:[],
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getCodeHistory(this.$route.query.type).then(res=>{
        console.log('历史版本',res)
        this.list = res.data
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>